html{height:100%}body{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:100%;background:#fff;margin:0;padding:0}.rss-menu,.rss-content{font-family:Source Sans Pro,Helvetica,sans-serif;height:100%}.rss-content{width:100%;margin-left:180px}.rss-menu{background:#EFEFEF;position:fixed}.rss-tabs{width:220px;padding:0 20px}.rss-menu>*{font-family:sans-serif;font-size:13px}.border{border:1px solid #fff}.button-list{list-style:none;padding:0}.button-list>li>a{padding-left:5px;display:inline-block;line-height:22px;text-decoration:none;color:inherit}.button-list>li:hover{background:rgba(0,0,0,0.05)}.fa:after{display:inline-block;content:'+';float:right;line-height:25px}.button-yellow{background:inherit;border:1px solid #ff9632;border-radius:4px;color:#f99632}li{padding:5px;cursor:pointer}.fa{display:block;font:normal normal normal 14px/1 FontAwesome;font-size:inherit;text-rendering:auto;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.button-tab{padding-left:18px}.discover-button{padding:10px 15px;width:100%;border:1px solid rgba(14,14,14,0.28);background:#EFEFEF;border-radius:4px}button:focus{outline:none}button:hover{cursor:pointer}.author-avatar{display:block;-webkit-box-flex:2;-ms-flex-positive:2;flex-grow:2}.author-avatar:hover{cursor:pointer}.author-avatar>img{width:25px;height:25px;border-radius:15px}.fixed-profile-board{position:fixed;bottom:0;left:0;width:240px;padding:10px 10px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex}.author-name{display:inline-block;-webkit-box-flex:2;-ms-flex-positive:2;flex-grow:2;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;max-width:140px}.upgrade-button{border-radius:4px;color:#fff;border:inherit;background-color:#F60;height:27px}


